"use client";

import { useState, useCallback } from "react";
import { useRouter } from "next/navigation";
import { toast } from "sonner";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Loader2, Percent, Package, Truck, MessageSquare, X } from "lucide-react";

interface NegotiationFormData {
  percentage: number | "";
  quantity: number | "";
  deliveryInfo: string;
  additionalInfo: string;
}

interface ValidationErrors {
  percentage?: string;
  quantity?: string;
  deliveryInfo?: string;
}

interface NegotiationPayloadData {
  SellerEmail: string;
  deliveryRelatedInfo: string;
  messages: string;
  negotiationValue: string;
  previewAmount: number;
  previewQty: number;
  yourQty: string;
  measurement: string;
}

interface OrderData {
  _id: string;
  status: string;
  productName: string;
  amount: number;
  buyer_email: string;
  commission: number;
  company: string;
  created_at: string;
  description: string;
  gstPercentage: number;
  measurement: string;
  minQty: number;
  mobile: string;
  negotiation: boolean;
  pid: number;
  pincode: string;
  reqId: string;
  seller_email: string;
  hsnCode: string;
  __v: number;
}

interface ProductData {
  _id: string;
  company: string;
  createdAt: string;
  created_at: string;
  email: string;
  gstNo: string;
  measurement: string;
  minQty: number;
  mobile: string;
  name: string;
  pid: number;
  pincode: string;
  reqId: string;
  specification: string;
  status: string;
  userType: string;
  __v: number;
}

interface NegotiationPayload {
  data: NegotiationPayloadData;
  orderData: OrderData;
  productData: ProductData;
}

interface NegotiationButtonProps {
  reqId: string;
  productName: string;
  amount: number;
  minQty: number;
  measurement: string;
  buyerEmail: string;
  sellerEmail: string;
  company: string;
  createdAt: string;
  description: string;
  gstPercentage: number;
  hsnCode: string;
  mobile: string;
  pincode: string;
  status: string;
  pid: number;
  specification?: string;
}

const NegotiationButton: React.FC<NegotiationButtonProps> = ({
  reqId,
  productName,
  amount,
  minQty,
  measurement,
  buyerEmail,
  sellerEmail,
  company,
  createdAt,
  description,
  gstPercentage,
  hsnCode,
  mobile,
  pincode,
  status,
  pid,
  specification,
}) => {
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState<NegotiationFormData>({
    percentage: "",
    quantity: "",
    deliveryInfo: "",
    additionalInfo: "",
  });
  const [validationErrors, setValidationErrors] = useState<ValidationErrors>({});
  const [submitting, setSubmitting] = useState(false);
  const router = useRouter();
  const API_URL = "http://localhost:3033/api/v1/";

  const getCookie = (name: string): string | null => {
    if (typeof document === "undefined") return null;
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop()?.split(";").shift() || null;
    return null;
  };

  const validateForm = useCallback((): boolean => {
    const errors: ValidationErrors = {};

    if (!formData.percentage || formData.percentage <= 0 || formData.percentage > 100) {
      errors.percentage = "Please enter a valid percentage between 1 and 100";
    }

    if (!formData.quantity || formData.quantity <= 0) {
      errors.quantity = "Please enter a valid quantity greater than 0";
    }

    if (!formData.deliveryInfo.trim()) {
      errors.deliveryInfo = "Delivery information is required";
    } else if (formData.deliveryInfo.trim().length < 10) {
      errors.deliveryInfo = "Please provide more detailed delivery information (minimum 10 characters)";
    }

    setValidationErrors(errors);
    return Object.keys(errors).length === 0;
  }, [formData]);

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;

    setFormData((prev) => ({
      ...prev,
      [name]: name === "percentage" || name === "quantity" ? (value ? Number(value) : "") : value,
    }));

    if (validationErrors[name as keyof ValidationErrors]) {
      setValidationErrors((prev) => ({
        ...prev,
        [name]: undefined,
      }));
    }
  };

  const handleSubmitNegotiation = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    e.stopPropagation();

    if (submitting) return;

    if (!validateForm()) {
      toast.error("Please fix the validation errors before submitting");
      return;
    }

    try {
      setSubmitting(true);

      const token = getCookie("token");
      if (!token) {
        toast.error("Authentication required. Please login again.");
        router.push("/");
        return;
      }

      const payload: NegotiationPayload = {
        data: {
          SellerEmail: sellerEmail,
          deliveryRelatedInfo: formData.deliveryInfo.trim(),
          messages: formData.additionalInfo.trim(),
          negotiationValue: formData.percentage.toString(),
          previewAmount: amount,
          previewQty: minQty,
          yourQty: formData.quantity.toString(),
          measurement: measurement,
        },
        orderData: {
          _id: reqId,
          status: status,
          productName: productName,
          amount: amount,
          buyer_email: buyerEmail,
          commission: 15, // Hardcoded as per previous example
          company: company,
          created_at: createdAt,
          description: description,
          gstPercentage: gstPercentage,
          measurement: measurement,
          minQty: minQty,
          mobile: mobile,
          negotiation: true,
          pid: pid,
          pincode: pincode,
          reqId: reqId,
          seller_email: sellerEmail,
          hsnCode: hsnCode,
          __v: 0,
        },
        productData: {
          _id: reqId, // Using reqId as a placeholder; adjust if a unique product ID is available
          company: company,
          createdAt: createdAt,
          created_at: createdAt,
          email: buyerEmail,
          gstNo: "24ACHFS7557F1ZV", // Hardcoded as per previous example
          measurement: measurement,
          minQty: minQty,
          mobile: mobile,
          name: productName,
          pid: pid,
          pincode: pincode,
          reqId: reqId,
          specification: specification || "Negotiation",
          status: status,
          userType: "buyer",
          __v: 0,
        },
      };

      const response = await fetch(`${API_URL}/negotiation`, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({
          message: "Failed to submit negotiation",
        }));
        throw new Error(errorData.message || "Failed to submit negotiation");
      }

      toast.success("Negotiation offer submitted successfully!");
      setOpen(false);
      router.push("/dashboard/tracking");
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : "Failed to submit negotiation";
      toast.error(errorMessage);
      console.error("Negotiation submission error:", err);
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button className="bg-blue-600 hover:bg-blue-700 text-white">
          Start Negotiation
        </Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-[600px] max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="flex items-center space-x-2">
            <MessageSquare size={20} className="text-blue-600" />
            <span>Negotiation Request</span>
          </DialogTitle>
          <Button
            variant="ghost"
            className="absolute right-4 top-4 text-gray-500 hover:text-gray-700"
            onClick={() => setOpen(false)}
          >
            <X size={20} />
          </Button>
        </DialogHeader>
        <div className="space-y-6 p-6">
          {/* Product Details */}
          <div className="border-b border-gray-200 pb-4">
            <h3 className="text-lg font-semibold text-gray-900 flex items-center space-x-2">
              <Package size={18} className="text-blue-600" />
              <span>Product Details</span>
            </h3>
            <div className="mt-4 grid grid-cols-1 sm:grid-cols-2 gap-4 text-sm">
              <div>
                <span className="text-gray-600 font-medium">Product Name:</span>
                <span className="ml-2 text-gray-900">{productName}</span>
              </div>
              <div>
                <span className="text-gray-600 font-medium">Minimum Quantity:</span>
                <span className="ml-2 text-gray-900">
                  {minQty} {measurement}
                </span>
              </div>
              <div>
                <span className="text-gray-600 font-medium">Amount:</span>
                <span className="ml-2 text-gray-900">₹{amount}</span>
              </div>
              <div>
                <span className="text-gray-600 font-medium">Status:</span>
                <span className="ml-2 text-gray-900 capitalize">{status}</span>
              </div>
            </div>
          </div>

          {/* Negotiation Form */}
          <form onSubmit={handleSubmitNegotiation} className="space-y-6">
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-900">Negotiation Details</h3>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label
                    htmlFor="percentage"
                    className="text-sm font-medium text-gray-700"
                  >
                    Your Percentage *
                  </Label>
                  <div className="relative mt-1">
                    <Input
                      id="percentage"
                      name="percentage"
                      type="number"
                      min="0.01"
                      max="100"
                      step="0.01"
                      value={formData.percentage}
                      onChange={handleInputChange}
                      placeholder="Enter percentage"
                      className={`border-gray-300 focus:border-blue-500 focus:ring-blue-500 pr-8 ${
                        validationErrors.percentage ? "border-red-500" : ""
                      }`}
                      required
                    />
                    <Percent
                      size={16}
                      className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-500"
                    />
                  </div>
                  {validationErrors.percentage && (
                    <p className="text-red-500 text-xs mt-1">
                      {validationErrors.percentage}
                    </p>
                  )}
                </div>
                <div>
                  <Label
                    htmlFor="quantity"
                    className="text-sm font-medium text-gray-700"
                  >
                    Your Quantity *
                  </Label>
                  <Input
                    id="quantity"
                    name="quantity"
                    type="number"
                    min="1"
                    step="1"
                    value={formData.quantity}
                    onChange={handleInputChange}
                    placeholder="Enter quantity"
                    className={`mt-1 border-gray-300 focus:border-blue-500 focus:ring-blue-500 ${
                      validationErrors.quantity ? "border-red-500" : ""
                    }`}
                    required
                  />
                  {validationErrors.quantity && (
                    <p className="text-red-500 text-xs mt-1">
                      {validationErrors.quantity}
                    </p>
                  )}
                </div>
              </div>
            </div>

            <div className="space-y-2">
              <Label
                htmlFor="deliveryInfo"
                className="text-sm font-medium text-gray-700 flex items-center space-x-2"
              >
                <Truck size={14} className="text-blue-600" />
                <span>Delivery Information *</span>
              </Label>
              <Input
                id="deliveryInfo"
                name="deliveryInfo"
                value={formData.deliveryInfo}
                onChange={handleInputChange}
                placeholder="Enter delivery information (e.g., address, timeline)"
                className={`border-gray-300 focus:border-blue-500 focus:ring-blue-500 ${
                  validationErrors.deliveryInfo ? "border-red-500" : ""
                }`}
                required
              />
              {validationErrors.deliveryInfo && (
                <p className="text-red-500 text-xs mt-1">
                  {validationErrors.deliveryInfo}
                </p>
              )}
            </div>

            <div className="space-y-2">
              <Label
                htmlFor="additionalInfo"
                className="text-sm font-medium text-gray-700"
              >
                Additional Information (Optional)
              </Label>
              <Textarea
                id="additionalInfo"
                name="additionalInfo"
                value={formData.additionalInfo}
                onChange={handleInputChange}
                placeholder="Any additional comments or requirements"
                className="border-gray-300 focus:border-blue-500 focus:ring-blue-500"
                rows={4}
              />
            </div>

            <div className="border-t border-gray-200 pt-4 flex justify-end space-x-3">
              <Button
                type="button"
                variant="outline"
                onClick={() => setOpen(false)}
                className="border-gray-300 text-gray-700 hover:bg-gray-100"
              >
                Cancel
              </Button>
              <Button
                type="submit"
                disabled={submitting}
                className="bg-blue-600 hover:bg-blue-700 text-white"
              >
                {submitting ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Submitting...
                  </>
                ) : (
                  "Submit Offer"
                )}
              </Button>
            </div>
          </form>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default NegotiationButton;